export const sortAscPrice = (data) => {
    return data.sort((a,b) => a.price-b.price)
}

export const sortDescPrice = (data) => {
    return data.sort((a,b) => b.price-a.price)
}

export const sortTravelTime = (data) => {
    return data.sort((a,b) => (a.there.duration + a.back.duration) - (b.there.duration + b.back.duration))
}

const choose = {
    '–ü–æ –≤–æ–∑—Ä–∞—Å—Ç–∞–Ω–∏—é —Ü–µ–Ω—ã': sortAscPrice,
    "–ü–æ —É–±—ã–≤–∞–Ω–∏—é —Ü–µ–Ω—ã": sortDescPrice,
    "–ü–æ –≤—Ä–µ–º–µ–Ω–∏ –≤ –ø—É—Ç–∏": sortTravelTime,
}

export const filterAndSort = (data, sortMethod, transferFilter, priceFilter) => {
    console.log(priceFilter)
    return choose[sortMethod](data.filter(el => {
        if ((+priceFilter.from && +el.price < +priceFilter.from) || (+priceFilter.to && +el.price > +priceFilter.to)) return false
        if ((!transferFilter['1 –ø–µ—Ä–µ—Å–∞–¥–∫–∞'] && (el.back.transferQuantity === 1 || el.there.transferQuantity === 1)) ||
            (!transferFilter['–±–µ–∑ –ø–µ—Ä–µ—Å–∞–¥–æ–∫'] && (el.back.transferQuantity === 0 || el.there.transferQuantity === 0))) return false
        return true
    }))
}

export const airCompaniesDataProcess = (filteredData, sortMethod) => {
    const searchedValue = (el) => {
        if (sortMethod === "–ü–æ –≤—Ä–µ–º–µ–Ω–∏ –≤ –ø—É—Ç–∏") return el.there.duration + el.back.duration
            return +el.price
    }
    return filteredData.reduce((acc,el) => {
        const compValue = searchedValue(el)
        if (!acc.hasOwnProperty(el.carrier) ||
            (sortMethod === "–ü–æ —É–±—ã–≤–∞–Ω–∏—é —Ü–µ–Ω—ã" && acc[el.carrier] <= compValue) ||
            (sortMethod !== "–ü–æ —É–±—ã–≤–∞–Ω–∏—é —Ü–µ–Ω—ã" && acc[el.carrier] >= compValue)) acc[el.carrier] = compValue
        return acc
        },{})
}

export const produceAirCompanyFormString = (sortMethod, company, value) => {
    const companyName = company.length > 19 ? company.slice(0, 14)+'....' : company
    if (sortMethod === "–ü–æ —É–±—ã–≤–∞–Ω–∏—é —Ü–µ–Ω—ã") return ` - ${companyName} –¥–æ ${value} —Ä.`
    if (sortMethod === "–ü–æ –≤—Ä–µ–º–µ–Ω–∏ –≤ –ø—É—Ç–∏") return ` - ${companyName} –æ—Ç ${Math.trunc(value/60)} —á ${value % 60} –º–∏–Ω`
    if (sortMethod === '–ü–æ –≤–æ–∑—Ä–∞—Å—Ç–∞–Ω–∏—é —Ü–µ–Ω—ã') return ` - ${companyName} –æ—Ç ${value} —Ä.`
}
